import React, {useState} from 'react'
import {Link, useLocation} from "react-router-dom";
import {LAB_ROUTE, REGISTRATION_ROUTE} from "../utils/consts";
import {Container, Input, LabelText, Col, MainTitleText} from "../styles/styled-components";
import TitleScreen from "../components/TitleScreen";
import {Button} from "react-bootstrap";
import {useNavigate} from "react-router";

function validateName(name) {
    return /[--\s]+/.test(name)
}

function validateGroup(group) {
    return true
    // return /^[--]\d[--\d]-\d\d\d[--]-\d\d$/.test(group)
}

const Auth = () => {
    const [name, setName] = useState("")
    const [group, setGroup] = useState("")
    const navigate = useNavigate()
    const isLogin = false

    function sendForm(name, group, navigate) {
        if (validateGroup(group) && validateName(name)) {
            navigate(LAB_ROUTE)
        } else {
            alert("  ")
        }
    }

    return (
        <Container style={{display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
            <Container style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                flexDirection: "column",
                width: 700,
                height: 500,
                borderRadius: 15,
                // border: "1px solid black",
                backgroundColor: "#89b6ef"
            }}>
                <MainTitleText style={{marginBottom: 40}}>    </MainTitleText>
                <Col style={{marginBottom: 20}}>
                    <LabelText style={{marginBottom: 5}}></LabelText>
                    <Input onChange={e => setName(e.target.value)} placeholder={"  "}/>
                </Col>
                <Col>
                    <LabelText style={{marginBottom: 5}}> </LabelText>
                    <Input onChange={e => setGroup(e.target.value)} placeholder={"8-405-19"}/>
                </Col>
                <Button style={{marginTop: 40}} variant="primary" onClick={() => sendForm(name, group, navigate)}></Button>
            </Container>
        </Container>
    );
};

export default Auth;