import React, {useState} from 'react';
import {Container, Input, MainText, MainTitleText, Row, SecondaryTitleText} from "../styles/styled-components";
import Nav from "../components/Nav";
import {Button} from "react-bootstrap";
import {useNavigate} from "react-router";
import {FIRSTPAGE} from "../utils/consts";

const Lab = () => {
    const [count1, setCount1] = useState("")
    const [count2, setCount2] = useState("")
    const [number1, setNumber1] = useState("")
    const [number2, setNumber2] = useState("")
    const navigate = useNavigate()

    function handleClick() {
        if (count1 && count2 && number1 && number2) {
            navigate(FIRSTPAGE)
        } else {
            alert(" !")
        }
    }

    return (
        <Container style={{display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
            <Container style={{width: 700}}>
                <Nav style={{margin: 15}}/>
                <div style={{
                    width: "100%",
                    // height: "100%",
                    backgroundColor: "white",
                    borderRadius: 15,
                    padding: 10,
                    margin: 15,
                    display: "flex",
                    // alignItems: "center",
                    flexDirection: "column",
                    gap: 10
                }}>
                    <MainTitleText> </MainTitleText>
                    <SecondaryTitleText>:</SecondaryTitleText>
                    <MainText>  <span
                        style={{fontWeight: 600, fontStyle: "italic"}}>=[a(i,j)]</span>  <span
                        style={{fontWeight: 600, fontStyle: "italic"}}>(m x n)</span>,    <span
                        style={{fontWeight: 600, fontStyle: "italic"}}>a(s,t)</span>,    
                         <span style={{fontWeight: 600, fontStyle: "italic"}}>(a+1)</span>.</MainText>
                    <SecondaryTitleText>:</SecondaryTitleText>
                    <MainText>    <span
                        style={{fontWeight: 600, fontStyle: "italic"}}>(a+1)</span>     
                        .</MainText>
                </div>
                <div style={{
                    width: "100%",
                    // height: "100%",
                    backgroundColor: "white",
                    borderRadius: 15,
                    padding: 10,
                    margin: 15,
                    display: "flex",
                    // alignItems: "center",
                    flexDirection: "column",
                    gap: 10
                }}>
                    <MainTitleText> </MainTitleText>
                    <Row style={{
                        justifyContent: "space-between",
                        alignItems: "center",
                        borderBottom: "1px solid black",
                        paddingBottom: 10
                    }}><MainText>  1 </MainText><Input
                        onChange={e => setCount1(e.target.value)} style={{border: "1px solid black", width: 55}}
                        placeholder={"2-7"}/></Row>
                    <Row style={{
                        justifyContent: "space-between",
                        alignItems: "center",
                        borderBottom: "1px solid black",
                        paddingBottom: 10
                    }}><MainText>  2 </MainText><Input
                        onChange={e => setCount2(e.target.value)} style={{border: "1px solid black", width: 55}}
                        placeholder={"2-7"}/></Row>
                    <Row style={{
                        justifyContent: "space-between",
                        alignItems: "center",
                        borderBottom: "1px solid black",
                        paddingBottom: 10
                    }}><MainText>  1 ,  <br/>   :</MainText><Input
                        onChange={e => setNumber1(e.target.value)} style={{border: "1px solid black", width: 55}}
                        placeholder={""}/></Row>
                    <Row style={{
                        justifyContent: "space-between",
                        alignItems: "center",
                        borderBottom: "1px solid black",
                        paddingBottom: 10
                    }}><MainText>  2 ,  <br/>   :</MainText><Input
                        onChange={e => setNumber2(e.target.value)} style={{border: "1px solid black", width: 55}}
                        placeholder={""}/></Row>
                    <Button variant={"dark"} onClick={() => handleClick()}></Button>
                </div>
            </Container>
        </Container>
    );
};

export default Lab;