import React, {useContext, useEffect, useState} from 'react';
import Nav from "../components/Nav";
import {
    Container,
    LabelText,
    MainText,
    MainTitleText,
    MathText,
    Row,
    SecondaryTitleText
} from "../styles/styled-components";
import {Button, Dropdown, Table} from "react-bootstrap";
import Second from "./Second";
import {observer} from "mobx-react-lite";
import {Context} from "../index";
import {SECONDPAGE, THIRDPAGE} from "../utils/consts";
import {useNavigate} from "react-router";
import DropdownComp from "../components/DropdownComp";
import styled from "styled-components";

// const getMatrix = (h, w, min, max) => [...Array(w)].map(x => [...Array(h)].map(b => Math.floor(Math.random() * (max - min + 1) + min)));

const SecondPage = observer(() => {

    const {matrix} = useContext(Context)
    const [input1, setInput1] = useState("")
    const [input2, setInput2] = useState("")
    const [input3, setInput3] = useState("")
    const [input4, setInput4] = useState("")
    const [input5, setInput5] = useState("")
    const [input6, setInput6] = useState("")
    // useEffect(() => {
    //     matrix.generateMatrix(4, 5, 0, 40)
    //
    // },[])
    // console.log(matrix)

    const navigate = useNavigate()

    return (
        <Container style={{display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
            <Container style={{width: 700}}>
                <Nav style={{margin: 15}}/>
                <div style={{
                    width: "100%",
                    // height: "100%",
                    backgroundColor: "white",
                    borderRadius: 15,
                    padding: 10,
                    margin: 15,
                    display: "flex",
                    // alignItems: "center",
                    flexDirection: "column",
                    gap: 10
                }}>
                    <MainTitleText> :</MainTitleText>
                    <div style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        gap: 30
                    }}>
                        <MainText>A =</MainText>
                        <Table striped style={{width: 200}}>
                            <tbody>
                            {matrix.values.map((row, i) =>
                                <tr>
                                    {row.map((cell, j) =>
                                        <td>{i === 0 && j === 0 ? "a+1" : cell}</td>
                                    )}
                                </tr>
                            )}
                            </tbody>
                        </Table>
                    </div>
                    <LabelText style={{color: "#ff5151"}}>         
                        </LabelText>
                    {/*<Button variant={"dark"} onClick={() => navigate(SECONDPAGE)}></Button>*/}
                </div>
                <SecondaryTitleText>   ,    
                     <MathText>(a+1)</MathText></SecondaryTitleText>
                <div style={{
                    display: "flex",
                    // alignItems: "center",
                    // justifyContent: "center",
                    gap: 10,
                    flexDirection: "column"
                }}>
                    <Row style={{alignItems: "center", gap: 10, height: 70}}>
                        <MathText>&alpha; = </MathText>
                        <DropdownComp title={""} items={["min", "max"]}/>
                        {/*<Brace>[</Brace>*/}
                        <DropdownComp title={""} items={["min", "max"]}/>
                        <MathText>a(i,j)</MathText>
                        {/*<Brace>]</Brace>*/}
                    </Row>
                    <Row style={{alignItems: "center", gap: 10, height: 70}}>
                        <MathText>&beta; = </MathText>
                        <DropdownComp title={""} items={["min", "max"]}/>
                        {/*<Brace>[</Brace>*/}
                        <DropdownComp title={""} items={["min", "max"]}/>
                        <MathText>a(i,j)</MathText>
                        {/*<Brace>]</Brace>*/}
                    </Row>
                <Button variant={"dark"} onClick={() => navigate(THIRDPAGE)}></Button>
                </div>
            </Container>
        </Container>
    );
});

export default SecondPage;

export const Brace = styled(MathText)`
  font-size: 40px;
  //line-height: 10;
`
